/* Airbending, work on adding more instances with variations to the animation */
.airbending {
  width: 100%;
  height: 100%;
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
