.airbending i {
  font-size: 5em;
  color: green;
  position: absolute;
  display: auto;
  z-index: 3;
  opacity: 0;
  -webkit-animation-name: airbending;
          animation-name: airbending;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}
@-webkit-keyframes airbending {
  0% {-webkit-transform: scale(1) rotate3d(1, 1, 0, -140deg);transform: scale(1) rotate3d(1, 1, 0, -140deg); opacity: 0; display: block;}
  10% {opacity: 1;}
  50% {-webkit-transform: scale(0.3) rotate3d(0, 1, 1, 0deg);transform: scale(0.3) rotate3d(0, 1, 1, 0deg);}
  90% {-webkit-transform: scale(0.2) rotate3d(1, 0, 0, 140deg);transform: scale(0.2) rotate3d(1, 0, 0, 140deg);}
  95% {opacity: 1;}
  100% {-webkit-transform: scale(0.2) rotate3d(1, 1, 0, 140deg);transform: scale(0.2) rotate3d(1, 1, 0, 140deg); opacity: 0; display: none;}
}
@keyframes airbending {
  0% {-webkit-transform: scale(1) rotate3d(1, 1, 0, -140deg);transform: scale(1) rotate3d(1, 1, 0, -140deg); opacity: 0; display: block;}
